body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'AraJozoor-Regular',Sans-Serif;
  width: 100%;
}

.App-Container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  -webkit-flex-flow: row-reverse wrap;
          flex-flow: row-reverse wrap;
}

.App-DatePicker{
  font-size: smaller;
  text-align: center;
  padding: 10px;
}

.App-block{
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 10px;
}

.App-header {
  background-color: rgb(63, 64, 65);
  font-family: 'AraJozoor-Regular',Sans-Serif;
  text-align: center;
  padding: 5px;
}

.App-body {
  text-align: center;
  font-family: 'droid-arabic-naskh';
  background-color: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(7px + 2vmin);
  color: black;
  padding: 10px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-footer{
  -webkit-flex: 1 1;
          flex: 1 1;
  font-size: smaller;
  text-align: center;
  font-family: droid-arabic-naskh;
}

.copyright {
  margin-top: 5px;
  font-family: 'Verdana, Geneva, sans-serif';
  font-size:12px;
  font-weight:bold;
  direction:ltr;
}
@font-face {
	font-family: 'AraJozoor-Regular';
	src: url(/market/static/media/AraJozoor-Regular.fa1f99dc.eot);
	src: local('\263A'), url(/market/static/media/AraJozoor-Regular.b573015b.woff) format('woff'), url(/market/static/media/AraJozoor-Regular.289cda26.ttf) format('truetype'), url(/market/static/media/AraJozoor-Regular.f2b8b184.svg) format('svg');
	font-weight: normal;
	font-style: normal;
}


.Countries {
  margin: 10px;
  min-width: 240px;
  font-family: 'AraJozoor-Regular',Sans-Serif;
}

.description {
  padding-Left: 10px;
  max-width: 350px;
  width: 100%;
}

.collapsibleHead {
  border-radius: 7px;
  padding: 5px ;
  text-align: center;
  min-width: 300px;
  width: 100%;
}

.collapsibleContent {
  padding: 5px ;
  text-align: left;
  width: 100%;
}

.imageCSS {
  max-Width: 300px;
  padding: 10px;
  width: 100%;
}
.priceBlock {
    display: -webkit-inline-flex;
    display: inline-flex;
}

.price {

}

.oldPrice {

}
.container {
    text-align: -webkit-center;
}

.MenuContainer {
    padding: 5px;
    display: inline;
}
.Image {
    display: inline;
    vertical-align: middle;
    width: 40px;
}
.Title {
    color: white;
    vertical-align: middle;
    font-size: large;
    padding-left: 50px;
    padding-right: 50px;
}
.container {
    padding-bottom: 5px;
    text-align: -webkit-center;
}

.textContainer {
    padding: 5px;
    display: inline;
    background-color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.input {
    display: inline;
    border-width: 0px;
    outline-color: white;
    vertical-align: middle;
    min-width: 250px;
    max-width: 500px;
    width: 80%;
}

.img {
    display: inline;
    margin-left: 5px;
    margin-right: 5px;
    vertical-align: middle;
    width: 25px;
}
